.menu-button__toggle,
.search-button__toggle {
  position: relative;
  z-index: 3;
  overflow: hidden;
  height: 2em;
  margin-top: 0.5em;
  padding: 0.2em 0.4em 0.4em 2em;
  text-transform: uppercase;
  color: #984a2d;
  background-color: transparent;
  border: none;
  transition: all 0.3s ease-in;
  cursor: pointer;
}

.menu-button__toggle:before,
.search-button__toggle:before {
  content: "";
  position: absolute;
  top: -1.5em;
  left: -1.5em;
  width: 5em;
  height: 5em;
  background-repeat: no-repeat;
  background-size: 35%;
  background-position: center center;
  transition: all 0.3s ease-in;
}

.menu-button__toggle:before {
  background-image: url("../images/icons/compass.svg");
  transform: rotate(0deg);
}

.search-button__toggle:before {
  background-image: url("../images/icons/search-sticky.svg");
}

.menu-button__toggle:hover {
  transition: all 0.3s ease-in;
}

.menu-button__toggle:hover:before {
  top: -1.55em;
  left: -1.55em;
  transform: rotate(45deg);
}

.search-button__toggle:hover {
  color: #984a2d;
  transition: all 0.3s ease-in;
}

.search-button__toggle:hover:before {
  top: -1.7em;
  left: -1.5em;
  transform: rotate(5deg);
}

